@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500&display=swap');

body {
 font-family: 'Open Sans', sans-serif;
 padding: 0;
 margin: 0;
}

.sider-menu {
    position: fixed;
    display: flex;
    flex-direction: column;
    left:0;
    right: 0;
    justify-content: space-between;
    font-weight: 600;
}

.navigation {
        z-index: 100;
        height: 100vh !important;
        box-shadow: 0px 4px 6px rgb(237 237 237 / 75%) #888;
}
.title:before,
.title:after {
  z-index: 1;
  background-color: #13c2c2;
  content: "";
  display: inline-block;
  height: 1px;
  position: relative;
  vertical-align: middle;
  width: 20%;
}

.title:before {
  right: 0.5em;
  margin-left: -50%;
}

.title:after {
  left: 0.5em;
  margin-right: -50%;
}